import style from "../Dialogs.module.css";
import {Field, reduxForm} from "redux-form";
import React from "react";
import {maxLengthCreator, required} from "../../../Utils/validators/validators";
import {Textarea} from "../../common/FormsControls/FormsControls";

const maxLength50 = maxLengthCreator(50)

const AddMessageForm = ({handleSubmit}) => {
    return (
        <form onSubmit={handleSubmit}>
            <div className={style.tb}>
                <Field component={Textarea} validate={[required, maxLength50]} name='NewMessageBody' placeholder=" "/>
                <button className={style.addMessage}></button>
            </div>
        </form>
    )
}// Field  input  . redux-form. Name -      .
//handleSubmit - .    redux-form.(   ., .. - 
//  e.preventDafault, ..    ),         
//   ,     props.onSubmit(formData( 
//  )),         .

export const AddMessageFormRedux = reduxForm({form: 'dialogAddMessageForm'})(AddMessageForm) //   AddMessageForm  AddMessageFormRedux,
//   mapStateToProps,        .   state  State.form.dialogAddMessageForm .
//  form       store, State.form.dialogAddMessageForm -  form   store.