import {usersAPI} from '../api/api'

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET_USERS';
const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE';
const SET_TOTAL_USERS_COUNT = 'SET_TOTAL_USERS_COUNT';
const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING';
const TOGGLE_IS_FOLLOWING_PROGRESS = 'TOGGLE_IS_FOLLOWING_PROGRESS';

let initialState = {
    users: [],
    pageSize: 10,
    totalUsersCount: 0,
    currentPage: 1,
    isFetching: true,
    followingInProgress: []
}

const UsersReducer = (state = initialState, action) => {
    switch (action.type) {
        case FOLLOW: {
            return {
                ...state,
                users: state.users.map(u => {
                    if (u.id === action.userId) {
                        return {...u, followed: true}
                    }
                    return u;
                })
            };
        }
        case UNFOLLOW: {
            return {
                ...state,
                users: state.users.map(u => {
                    if (u.id === action.userId) {
                        return {...u, followed: false}
                    }
                    return u;
                })
            };
        }
        case SET_USERS: {
            return {
                ...state, users: action.users
            }
        }
        case SET_CURRENT_PAGE: {
            return {
                ...state, currentPage: action.currentPage
            }
        }
        case SET_TOTAL_USERS_COUNT: {
            return {
                ...state, totalUsersCount: action.count
            }
        }
        case TOGGLE_IS_FETCHING: {
            return {
                ...state, isFetching: action.isFetching
            }
        }
        case TOGGLE_IS_FOLLOWING_PROGRESS: {
            return {
                ...state,
                followingInProgress: action.isFetching
                    ? [...state.followingInProgress, action.userId]
                    : state.followingInProgress.filter(id => id !== action.userId)
            }
        }
        default:
            return {
                ...state
            }
    }
}

export const followSuccess = (userId) => ({type: FOLLOW, userId}); // userId: userId,          .
export const unfollowSuccess = (userId) => ({type: UNFOLLOW, userId});
export const setUsers = (users) => ({type: SET_USERS, users});
export const setCurrentPage = (currentPage) => ({type: SET_CURRENT_PAGE, currentPage});
export const setTotalUsersCount = (totalUsersCount) => ({type: SET_TOTAL_USERS_COUNT, count: totalUsersCount});
export const toggleIsFetching = (isFetching) => ({type: TOGGLE_IS_FETCHING, isFetching});
export const toggleIsFollowingProgress = (isFetching, userId) => ({
    type: TOGGLE_IS_FOLLOWING_PROGRESS,
    isFetching,
    userId
});

//---Thunk---
export const requestUsers = (page, pageSize) => { //      ,      2 . getUsers    81.
    return async (dispatch) => { // thunk ,  -           .
        dispatch(toggleIsFetching(true)); //   .  action = { type: TOGGLE_IS_FETCHING, isFetching }     UsersReducer.
        let data = await usersAPI.getUsers(page, pageSize); // ?page - , getUsers() -     (  api,  DAL - data axcess layer,   .   ).   data,    response,    .
        dispatch(toggleIsFetching(false)); //   .( actioncreator-   action )
        dispatch(setCurrentPage(page));
        dispatch(setUsers(data.items)); //  setUsers   .
        dispatch(setTotalUsersCount(data.totalCount)); //  setTotalUsersCount   - .
    }
} //         ,          ,     
// ,    dispatch-  (..  action,    ),      UsersReducer,      
// DAL      .

export const follow = (userId) => { //      ,      2 .
    return async (dispatch) => { // thunk ,  -           .
        dispatch(toggleIsFollowingProgress(true, userId)); //  . 
        let response = await usersAPI.follow(userId); //  id       api.js( DAL )
        if (response.data.resultCode === 0) { //  ,      state,   ,   response       resultCode:0,    resultCode:1
            dispatch(followSuccess(userId));
        }
        dispatch(toggleIsFollowingProgress(false, userId));
    }
}

export const unfollow = (userId) => { //      ,      2 .
    return (dispatch) => { // thunk ,  -           .
        dispatch(toggleIsFollowingProgress(true, userId)); // .
        usersAPI.unfollow(userId) //   .  api.js( DAL )
            .then(response => {
                if (response.data.resultCode === 0) {
                    dispatch(unfollowSuccess(userId));
                }
                dispatch(toggleIsFollowingProgress(false, userId)); //      .
            });//   follow/unfollow  . {withCredentioals: true -  . headers:...    .
    }
}

export default UsersReducer;