import React from "react";
import {Field, reduxForm} from "redux-form";
import {Input} from "../common/FormsControls/FormsControls";
import {required} from "../../Utils/validators/validators";
import {connect} from "react-redux";
import {login} from "../../Redux/AuthReducer";
import {Navigate} from "react-router-dom";
import style from './../common/FormsControls/FormsControls.module.css'

const LoginForm = ({handleSubmit, error}) => {
    return ( // Field  input  . redux-form. Name -      .
        //handleSubmit - .    redux-form.(   ., .. - 
        //  e.preventDafault, ..    ),         
        //   ,     props.onSubmit(formData( 
        //  )),         .
        <form onSubmit={handleSubmit}>
            <div>
                <Field placeholder={'Email'}
                       name={'email'}
                       validate={[required]}
                       component={Input}/>
            </div>
            <div>
                <Field placeholder={'Password'}
                       type={'password'}
                       name={'password'}
                       validate={[required]}
                       component={Input}/>
            </div>
            <div>
                <Field type={"checkbox"}
                       name={'rememberMe'}
                       component={Input}/> remember me
            </div>
            {error && <div className={style.formSummaryError}>{error}</div>}
            <div>
                <button>Login</button>
            </div>
        </form>)
}

const LoginReduxForm = reduxForm({ //   LoginForm  ,   mapStateToProps.
    form: 'login' //        .   state  State.form.Login .
    //  form       store, State.form.Login -  form   store.
})(LoginForm)

const Login = (props) => { //     login( thunk creator,   connect- ,      ).
    const onSubmit = (formData) => {
        props.login(formData.email, formData.password, formData.rememberMe)
    }

    if(props.isAuth) { //  ,     Profile.
        return <Navigate to={'/profile'} />
    }
    return <div>
        <h1>Login</h1>
        <LoginReduxForm onSubmit={onSubmit}/>
    </div>
}

const mapStateToProps = (state) => {
    return {
        isAuth: state.auth.isAuth
    }
}

export default connect(mapStateToProps, {login})(Login); //  Login    AuthReducer,    thunk creator.