import React from "react";
import { Navigate } from "react-router-dom";
import { connect } from "react-redux";

//      - with.
//  HOC      ,     .

let mapStateToPropsForRedirect = (state) => ({
    isAuth: state.auth.isAuth
})

export const WithAuthRedirect = (Component) => { //  . HOC,    ,   .
    class RedirectComponent extends React.Component {
        render() {
            if(!this.props.isAuth) return <Navigate to={'/login'} /> //   react-router-dom     Login,   (isAuth = false)
            return <Component {...this.props}/> //       props.
        }
    }
    
    let ConnectedAuthRedirectComponent = connect(mapStateToPropsForRedirect)(RedirectComponent); //        isAuth

    return ConnectedAuthRedirectComponent;
}