import {authAPI} from '../api/api'
import {stopSubmit} from "redux-form";

const SET_USER_DATA = 'SET_USER_DATA';

let initialState = {
    userId: null,
    email: null,
    login: null,
    isAuth: false
} // isAuth -     .

const AuthReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA: {
            return {
                ...state,
                ...action.payload // ..
            }
        }
        default:
            return {
                ...state
            }
    }
}

export const setAuthUserData = (userId, email, login, isAuth) => ({
    type: SET_USER_DATA,
    payload: {userId, email, login, isAuth}
}); //  .

//   thunk creator:
export const getAuthUserData = () => async (dispatch) => {
    let response = await authAPI.me(); //  promise.
    if (response.data.resultCode === 0) { // resultCode  .    . , 0 - , 1 - .
        let {id, login, email} = response.data.data; //  .
        dispatch(setAuthUserData(id, email, login, true)); //  ,    !
    }
}

export const login = (email, password, rememberMe) => async (dispatch) => { //    Login.jsx.
    let response = await authAPI.login(email, password, rememberMe); //    ., withCredentials  .
    if (response.data.resultCode === 0) { // resultCode  .    . , 0 - , 1 - (  .     ).
        dispatch(getAuthUserData())
    } else {
        let message = response.data.messages.length > 0 ? response.data.messages[0] : 'Some error'; //           ,  message     .
        dispatch(stopSubmit('login', {_error: message})); //.   redux-form,   submit .    'login',        .
    }
}

export const logout = () => async (dispatch) => { //   ,     .
     let response = await authAPI.logout(); //    ., withCredentials  .
        if (response.data.resultCode === 0) { // resultCode  .    . , 0 - , 1 - .
            dispatch(setAuthUserData(null, null, null, false)); // .
        }
}

export default AuthReducer;