import axios from "axios"

const instance = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {'API-KEY': '51fb1dfe-02fa-4bd1-8b1f-de41164483b5'}
}); //  axios,   - .

//         ,   .
//             ,    .
export const usersAPI = {
    getUsers(currentPage = 1, pageSize = 10) {
        return instance.get(`users?page=${currentPage}&count=${pageSize}`) //    - (baseUrl + `users?page=${currentPage}&count=${pageSize}`)(    baseURL)
        .then(response => response.data); //    response,   data( respomse). instance.get -  ,       ,     .
    },
    follow(userId) {
        return instance.post(`follow/${userId}`)
    },
    unfollow(userId) {
        return instance.delete(`follow/${userId}`)
    },
    getProfile(userId) {
        return profileAPI.getProfile(userId)
    }//  .
}

//-----------
export const profileAPI = {
    getProfile(userId) {
        return instance.get(`profile/` + userId);
    },
    getStatus(userId) {
        return instance.get(`profile/status/` + userId);
    },//    ,       .
    updateStatus(status) {
        return instance.put(`profile/status`, {status: status}); //  {status}, ..   . .
    },//    ,     id.   json , .  .
}

export const authAPI = {
    me() {
        return instance.get(`auth/me`);
    },
    login(email, password, rememberMe = false) {
        return instance.post(`auth/login`, { email, password, rememberMe }); //     .    .
            //       ,    .
    },
    logout(email, password, rememberMe = false) {
        return instance.delete(`auth/login`); //     .    .
        //       ,    .
    }
} 



