import {applyMiddleware, combineReducers, compose, legacy_createStore} from 'redux';
import profileReducer from './ProfileReducer';
import dialogsReducer from './DialogsReducer';
import sidebarReducer from './SidebarReducer';
import usersReducer from './UsersReducer';
import authReducer from './AuthReducer';
import {reducer as formReducer} from 'redux-form'; //     .
import {thunk} from 'redux-thunk';
import AppReducer from "./AppReducer";

let reducers = combineReducers({
    profilePage: profileReducer,
    dialogsPage: dialogsReducer,
    sidebar: sidebarReducer,
    usersPage: usersReducer,
    auth: authReducer,
    app: AppReducer,
    form: formReducer
})

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose; //    redux   . 2 .  .
const store = legacy_createStore(reducers, composeEnhancers(applyMiddleware(thunk)));

// let store = legacy_createStore(reducers, applyMiddleware(thunk)); //   . applyMiddleware -   , ..
// //      redux?    ,    (thunk ).
// , ..    ,    .

export default store;